<script>
  export let iconUrl;
  export let height, width;
  export let dx, dy;
  export let foreground, foregroundactive, background;
  export let border, hover, active, pressedstate;
  export let pressed;

  $: style = `--height: ${height}; --width: ${width}; --dx: ${dx}; --dy: ${dy}; --icon: url("${iconUrl}"); --background: ${background}; --foreground: ${foreground}; --foregroundactive: ${foregroundactive}; --width: ${width}; --hover: ${hover}; --active: ${active}; --pressedstate: ${pressedstate}; --border: ${border}`;

</script>

<style>
  .IconButton {
    height: var(--height);
    width: var(--width);
    position: relative;
    margin: 0;
    cursor: pointer;
    padding: 0;
    border-radius: 100%;
    border: 1px solid var(--border);
    background: var(--background);
    /* transition: background-color 100ms ease-out; */
    user-select: none;
    box-shadow: 0 0 1px 0 var(--border) inset, 0 0 1px 0 var(--border);
    outline: none;
    pointer-events: visible;
  }

  .IconButton:hover {
    background: var(--hover);
  }

  .IconButton:active {
    background: var(--active);
  }

  .IconButton:active:before {
    background: var(--foregroundactive);
  }

  .IconButton::before {
    /* Positioning related code */
    content: "";
    display: block;
    width: 50%;
    height: 50%;
    background: var(--foreground);

    /* this allows optical centering if required */
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(var(--dx), var(--dy));

    /* Masking code */
    mask-image: url('assets/svg/stop.svg');
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: 100%;

    -webkit-mask-image: url('assets/svg/stop.svg');
    -webkit-mask-position: center;
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-size: 100%;
  }

  .pressed {
    background: var(--pressedstate);
    animation: pulse-red 1.25s infinite;
  }

  @keyframes pulse-red {
    0% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 82, 82, 0.7);
    }

    70% {
      transform: scale(1);
      box-shadow: 0 0 0 10px rgba(255, 82, 82, 0);
    }

    100% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 rgba(255, 82, 82, 0);
    }
  }
</style>

<button class="IconButton" {style} class:pressed on:click />
