<script>
  import { flip } from "svelte/animate";
  import { createEventDispatcher } from "svelte";

  export let data = {};
  export let CustomComponent;

  let ghost;
  let grabbed;

  let swapped;

  let lastTarget;
  let lastIndex;

  let mouseY = 0; // pointer y coordinate within client
  let offsetY = 0; // y distance from top of grabbed element to pointer
  let layerY = 0; // distance from top of list to top of client

  const dispatch = createEventDispatcher();

  function grab(clientY, element) {
    // modify grabbed element
    grabbed = element;
    grabbed.dataset.grabY = clientY;

    // modify ghost element (which is actually dragged)
    // form elements are problematic, so an ugly hack is required here
    const inputTextValue = grabbed.querySelector("input[type='text']").value;

    // store state of the checkboxes in a map
    const inputCheckboxes = Array.prototype.slice.apply(
      grabbed.querySelectorAll("input[type='checkbox']")
    );
    let chkMap = new Map();
    inputCheckboxes.forEach((chk) => {
      chkMap[chk.value] = chk.checked;
    });

    const doc = new DOMParser().parseFromString(grabbed.innerHTML, "text/html");
    // apply attributes
    // input[type='text']
    doc
      .querySelector("input[type='text']")
      .setAttribute("value", inputTextValue);

    // input[type='checkbox']
    let chkBoxes = Array.prototype.slice.apply(
      doc.querySelectorAll("input[type='checkbox']")
    );
    chkBoxes.forEach((chk) => {
      if (chkMap[chk.value] === true) {
        chk.setAttribute("checked", null);
      } else {
        chk.removeAttribute("checked");
      }
    });

    ghost.innerHTML = doc.querySelector("body").innerHTML;

    // record offset from cursor to top of element
    // (used for positioning ghost)
    offsetY = grabbed.getBoundingClientRect().y - clientY;
    drag(clientY);
  }

  // drag handler updates cursor position
  function drag(clientY) {
    if (grabbed) {
      mouseY = clientY;
      layerY = ghost.parentNode.getBoundingClientRect().y;
    }
  }

  // touchEnter handler emulates the mouseenter event for touch input
  // (more or less)
  function touchEnter(ev) {
    drag(ev.clientY);
    // trigger dragEnter the first time the cursor moves over a list item
    let target = document
      .elementFromPoint(ev.clientX, ev.clientY)
      .closest(".item");
    if (target && target != lastTarget) {
      lastTarget = target;
      dragEnter(ev, target);
    }
  }

  function dragEnter(ev, target) {
    // swap items in data
    if (grabbed && target != grabbed && target.classList.contains("item")) {
      moveDatum(
        parseInt(grabbed.dataset.index),
        parseInt(target.dataset.index)
      );
    }
  }

  // does the actual moving of items in data
  function moveDatum(from, to) {
    let temp = data.messages[from];
    data.messages = [
      ...data.messages.slice(0, from),
      ...data.messages.slice(from + 1),
    ];
    data.messages = [
      ...data.messages.slice(0, to),
      temp,
      ...data.messages.slice(to),
    ];
    swapped = true;
    lastIndex = to;
  }

  function release(ev) {
    grabbed = null;
    if (swapped) {
      console.log("last index: ", lastIndex);
      dispatch("shouldsave", { dropIndex: lastIndex });
    }
    swapped = null;
  }
</script>

<style>
  main {
    position: relative;
  }

  .list {
    /* z-index: 5; */
    display: flex;
    flex-direction: column;
  }

  .item {
    display: flex;
    width: 100%;
    box-sizing: border-box;
    padding-right: 0px;
    width: 100%;
    min-height: 3em;
    background-color: var(--pagebackground);
    user-select: none;
  }

  .item:last-child {
    margin-bottom: 0;
  }

  /* .item:not(#grabbed):not(#ghost) {
    z-index: 10;
  } */

  .item > * {
    margin: auto;
  }

  #grabbed {
    opacity: 0;
  }

  #ghost {
    pointer-events: none;
    z-index: -5;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
  }

  #ghost * {
    pointer-events: none;
    border: solid 1px var(--basharqred);
  }

  #ghost.haunting {
    z-index: 20;
    opacity: 0.7;
  }
</style>

<!-- All the documentation has to go up here, sorry.
     (otherwise it conflicts with the HTML or svelte/animate) 
     The .list has handlers for pointer movement and pointer up/release/end.
     Each .item has a handler for pointer down/click/start, which assigns that
     element as the item currently being "grabbed".  They also have a handler
     for pointer enter (the touchmove handler has extra logic to behave like the
     no longer extant 'touchenter'), which swaps the entered element with the
     grabbed element when triggered.
     You'll also find reactive styling below, which keeps it from being directly
     part of the imperative javascript handlers. -->
<main class="dragdroplist">
  <div
    bind:this={ghost}
    id="ghost"
    class={grabbed ? 'item haunting' : 'item'}
    style={'top: ' + (mouseY + offsetY - layerY) + 'px'}>
    <p />
  </div>
  <div
    class="list"
    on:mousemove={function (ev) {
      ev.stopPropagation();
      drag(ev.clientY);
    }}
    on:touchmove={function (ev) {
      ev.stopPropagation();
      drag(ev.touches[0].clientY);
    }}
    on:mouseup={function (ev) {
      ev.stopPropagation();
      release(ev);
    }}
    on:touchend={function (ev) {
      ev.stopPropagation();
      release(ev.touches[0]);
    }}>
    {#each data.messages as datum, i (datum.id)}
      <div
        id={grabbed && (datum.id ? datum.id : JSON.stringify(datum)) == grabbed.dataset.id ? 'grabbed' : ''}
        class="item {datum.grouped && (i-1 >= 0 && !data.messages[i-1].grouped || i === 0) ? 'firstitem' : ''} {datum.grouped && (i+1 < data.messages.length && !data.messages[i+1].grouped || i === data.messages.length-1) ? 'lastitem' : ''}"
        data-index={i}
        data-id={datum.id ? datum.id : JSON.stringify(datum)}
        data-grabY="0"
        on:mousedown={function (ev) {
          grab(ev.clientY, this);
        }}
        on:touchstart={function (ev) {
          grab(ev.touches[0].clientY, this);
        }}
        on:mouseenter={function (ev) {
          ev.stopPropagation();
          dragEnter(ev, ev.target);
        }}
        on:touchmove={function (ev) {
          ev.stopPropagation();
          ev.preventDefault();
          touchEnter(ev.touches[0]);
        }}
        animate:flip={{ duration: 200 }}>
        <svelte:component
          this={CustomComponent}
          textField={datum.text}
          tags={datum.tags}
          id={datum.id}
          tabindex={i + 1}
          author={datum.author}
          charlimit={datum.template ? datum.template.charLimit : '(...)'}
          onair={datum.onair}
          cued={datum.cued}
          template={datum.template}
          selected={datum.selected}
          grouped={datum.grouped}
          on:msgfocus
          on:msgblur
          on:clicked
          on:uncue
          on:keyup
          on:keydown
          on:delete
          on:markdelete
          on:keypress
          on:templateselected />
      </div>
    {/each}
  </div>
</main>
