<script>
  export let label;
  export let text;

  export let bgColor="white";
  export let color="var(--basharqred)";
</script>

<style>
  button {
    background-color:var(--bgColor);
    border: 1px solid var(--basharqred);
    border-radius: 15px;
    color: var(--color);
    width: 10em;
    padding: 0px 1em;
    text-align: center;
    text-decoration: none;
    display: block;
    font-size: 12px;
    font-weight: 700;
    line-height: 25px;
    user-select: none;
    margin: 4px 2px;
    outline: none;
    font-family: 'Bloomberg Arabic Beta v4', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
      Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
      "Segoe UI Symbol"!important;
  }

  button:hover {
    box-shadow: 0px 4px 6px 0 rgba(102, 102, 102, 0.21);
    cursor: pointer;
  }

  button:active {
    box-shadow: 0px 2px 3px 0 rgba(102, 102, 102, 0.21);
    position: relative;
    top: 1px;
    cursor: pointer;
  }

</style>

<button style = "--color: {color}; --bgColor: {bgColor}" class="fab" on:click>{label} {text}</button>
