<script>
	// import the custom store
	import { center } from './notifications';

	// open-close state
	let show = false;
	const handleDismiss = (message) => {
		center.dismiss(message);
		// guard to close popup when there are no more messages
		if ($center.length === 0) {
			show = false;
		}
	};
	const handlePopup = () => {
		// don't show popup when no messages
		if ($center.length === 0) {
			show = false;
		} else {
			show = !show;
		}
	};
	const clearAll = () => {
		center.clear();
		// close popup
		show = false;
	};
	// limit the number of displayed messages to 5
	export let count = 5;

	// create a list of messages to display
	$: messages = $center.slice(0, count);

</script>

<style>
	/* unread message count */
	.badge {
		display: inline-block;
		position: absolute;
		top: 0;
		background-color: #4285f4;
		color: #d7e6fd;
		right: 0.8rem;
		border-radius: 9999px;
		font-size: 0.9em;
		min-width: 1.2rem;
		line-height: 1.5em;
		min-height: 1rem;
		text-align: center;
	}

  .pressed {
    background: var(--pressedstate);
    animation: pulse-red 1.25s 120;
  }

  @keyframes pulse-red {
    0% {
      /* transform: scale(0.95); */
      box-shadow: 0 0 0 0 rgba(255, 82, 82, 0.7);
    }

    70% {
      /* transform: scale(1.0); */
      box-shadow: 0 0 0 16px rgba(255, 82, 82, 0);
    }

    100% {
      /* transform: scale(0.95); */
      box-shadow: 0 0 0 0 rgba(255, 82, 82, 0);
    }
  }

	.notifyBtn {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url('assets/svg/bell.svg') no-repeat;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
	}

	/* .notifyBtn:active {
		border: none;
	} */

	.notifyBtnclose {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url("assets/svg/close.svg") no-repeat;
		fill: white;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
	}

	.messages {
		z-index: 20;
		position: absolute;
		right: 1rem;
		font-size: 0.8rem;
		text-align: left;
		width: 20vw;
		min-width: 24ch;
		max-width: 42ch;
		border: 1px solid var(--basharqred);
		background-color: white;
		box-shadow: 0px 16px 20px 0px rgba(0, 0, 0, 0.19);
		margin-top: 2rem;
		border-radius: 0px 15px 0px 15px;
	}

	.closeBtn {
		position: fixed;
		right: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		border: none;
		cursor: pointer;
		overflow: hidden;
		outline: none;
		background: transparent;
		z-index: 20;
	}

	.dismissBtn {
		margin-bottom: 0.5rem;
		padding: 0px 0.75rem;
		height: 1.5rem;
		background-color: white;
		border: 1px solid var(--basharqred);
		border-radius: 15px;
		box-sizing: border-box;
		font-family: 'Bloomberg Arabic Beta v4', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial,
			sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol' !important;
		font-size: 10px;
		font-weight: 100;
		color: var(--basharqred);
		text-align: center;
		line-height: 1rem;
		user-select: none;
		font-weight: 700;
		outline: none;
	}

	.dismissBtn:hover {
		box-shadow: 0px 4px 6px 0 rgba(102, 102, 102, 0.21);
		cursor: pointer;
	}

	.dismissAllBtn {
		margin-top: 1rem;
		margin-bottom: 0.5rem;
		display: flex;
		justify-content: center;
	}

	.dismissWrap {
		float: right;
	}

	ul {
		margin: 0px;
		padding-right: 2em;
		padding-left: 2em;
	}

	li {
		display: inline-block;
		width: 100%;
		margin: 0px;
		color: black;
		list-style: none;
		border-bottom: lightgrey 1px solid;
	}

	p {
		word-wrap: break-word;
	}

</style>

<button class={show ? "notifyBtnclose" : "notifyBtn"}  class:pressed="{messages.length > 0}" on:click|stopPropagation={handlePopup}>
	<!-- show number of messages or hide if zero -->
	{#if $center.length}
		<span class="badge">{$center.length}</span>
	{/if}

</button>

<!-- show only if there are messages -->
{#if show && $center.length}
	<!-- clicking anywhere on the page will close the popup -->
	<button tabindex="-1" class="closeBtn" on:click|preventDefault={() => (show = false)} />

	<div class="messages">
		<ul>
			<!-- loop through the message list -->
			{#each messages as message}
				<li>
					<p>
						{@html message}
					</p>
					<div class="dismissWrap">
						<!-- add dismiss handler -->
						<button class="dismissBtn" on:click={() => handleDismiss(message)}>dismiss</button>
					</div>
				</li>
			{/each}
		</ul>
		{#if messages.length > 1}
			<div class="dismissAllBtn">
				<!-- add clear all handler -->
				<button class="dismissBtn" on:click={clearAll}>dismiss all</button>
			</div>
		{/if}

	</div>
{/if}
