<script>
	import { tick, createEventDispatcher } from 'svelte';

	import { selectTextOnFocus } from './Selecttextonfocus.js';
	import { formatTitle, toDateTime } from './Formatitle.js';

	export let title;
	export let disabled = true;

	let thisInput;
	const dispatch = createEventDispatcher();

	$: formattedTitle = formatTitle(title);

	function deformat(t) {
		let tm = Date.parse(t);
		if (isNaN(tm)) {
			return t;
		} else {
			let dt = toDateTime(tm / 1000);
			return dt.toISOString();
		}
	}

	function handleNewDate() {
		const d = new Date();
		title = d.toISOString();
	}

	async function handleClick(p, event) {
		disabled = !disabled;
		await tick();
		if (!disabled) {
			thisInput.focus();
		} else {
			dispatchTitle(formattedTitle);
		}
	}

	function handleKeydown(event) {
		let key = event.key;
		let keyCode = event.keyCode;
		if (keyCode == 13 && event.target && event.target === thisInput) {
			disabled = true;
			dispatchTitle(formattedTitle);
		}
	}

	function dispatchTitle(t) {
		const df = deformat(t);
		dispatch('title', {
			title: df,
		});
		title = df;
	}
</script>

<style>
	input[type='text'] {
		display: block;
		margin-left: auto;
		margin-right: auto;
		border: 0px;
		width: 90%;
		font-size: 1.5rem;
		font-weight: 700;
		box-sizing: content-box;
		padding: 0.6rem;
		direction: initial;
		text-align: right 	;
		border: 1px solid #e7d9d9;
	}

	@media only screen and (min-width: 960px) {
		div {
			display: flex;
			align-items: center;
			margin-bottom: 2rem;
		}

		input[type='text'] {
			margin: 0 11px 0 0;
			border: 0px;
			width: 50%;
			font-size: 1.5rem;
			font-weight: 700;
			box-sizing: content-box;
			padding: 0.6rem;
			border: 1px solid #e7d9d9;
		}

		input[type='text']:focus {
			/* border-right: solid 4px var(--basharqred); */
			padding-right: 10px;
			transition: 0.2s ease-out;
			outline: 1px dashed var(--basharqred);
		}

		.btn {
			width: 2rem;
			height: 2rem;
			vertical-align: sub;
			transition: 0.1s;
			margin-top: 2px;
			margin-left: 5px;
			margin-bottom: 0;
			cursor: pointer;
		}

		.btn:hover {
			opacity: 0.7;
			transition: 0.1s;
		}

		.btn:active {
			background-color: white;
			opacity: 1;
			transition: 0.1s;
		}

		.btn .inner {
			margin-bottom: 0;
		}
		.btn .inner2 {
			margin-bottom: 0;
		}

		.inner {
			width: 32px;
			height: 32px;
			background: url('assets/svg/edit.svg') no-repeat center;
			background-size: 32px;
		}

		.inner2 {
			width: 32px;
			height: 32px;
			background: url('assets/svg/clock.svg') no-repeat center;
			background-size: 32px;
		}

		.active {
			visibility: initial;
		}

		.inactive {
			display: none;
		}
	}
</style>

<svelte:window on:keydown={handleKeydown} />

<div>
	<div class="btn" on:click={() => handleClick('edit')}>
		<div class="inner">

		</div>
	</div>
	<div class="btn {disabled ? 'inactive' : 'active'}" on:click={handleNewDate}>
		<div class="inner2">

		</div>
	</div>
	<input type="text" bind:value={formattedTitle} {disabled} bind:this={thisInput} use:selectTextOnFocus />
</div>
