<script>
	import { fade } from "svelte/transition";
	import { flip } from "svelte/animate";

	import { beforeUpdate, afterUpdate } from "svelte";

	import { createEventDispatcher } from "svelte";
	const dispatch = createEventDispatcher();

	import { UserState } from "../../enums.js";
	import Userpill from "./Userpill.svelte";
	import shortenAuthor from "../ShortenAuthor";
	import deepClone from "../../deepclone";

	// import the custom store
	import { chatmessages, users } from "./stores";

	import message from "./message";

	// limit the number of displayed messages to 125
	export let count = 125;
	export let user;

	// open-close state
	let hide = true;

	let div;
	let autoscroll;

	beforeUpdate(() => {
		autoscroll =
			div && div.offsetHeight + div.scrollTop > div.scrollHeight - 20;
	});

	afterUpdate(() => {
		if (autoscroll) div.scrollTo(0, div.scrollHeight);
	});

	function dateNow() {
		const d = new Date();
		return d.toISOString();
	}

	function handleKeydown(event) {
		if (event.key === "Enter") {
			const text = event.target.innerText;
			if (!text) return;
			const msg = new message(
				user,
				"",
				text.trim(),
				dateNow(),
				undefined,
				true
			);
			chatmessages.add(msg);
			// emit mg as unread to other connected clients
			let unreadMsg = deepClone(msg);
			unreadMsg.read = false;
			dispatch("emitmsg", unreadMsg);
			event.target.innerHTML = "";
		}
	}

	const handlePopup = () => {
		hide = !hide;
	};

	function formatDateTime(datetime) {
		let dt = "";
		const d = new Date(datetime);
		if (d.getTime() === d.getTime()) {
			dt = d.toLocaleTimeString();
		}
		return dt;
	}

	// create a list of messages to display
	$: messages = $chatmessages.slice(-count);
	$: if (!hide) {
		$chatmessages.forEach((m) => (m.read = true));
		$chatmessages = $chatmessages;
	}

</script>

<button
	class={hide ? "notifyBtn" : "notifyBtnclose"}
	class:blinking={messages.filter((m) => m.read === false).length > 0}
	on:click={handlePopup}>
	<!-- show number of messages or hide if zero -->
	{#if messages.filter((m) => m.read === false).length}
		<span class="badge">{messages.filter((m) => m.read === false).length}
		</span>
	{/if}
</button>

<div class="chat" class:hidden={hide}>
	<!-- <button
		tabindex="-1"
		class="closeBtn"
		on:click|preventDefault={() => {
			hide = true;
		}}
	/> -->
	<header>
		{#each $users as user (user.id)}
			<span transition:fade>
				<Userpill user={user.username} active={UserState.Active} />
			</span>
		{/each}
	</header>
	<div class="scrollable" bind:this={div}>
		{#each messages as comment (comment.id)}
			<article
				class={comment.author !== user ? "other" : ""}
				in:fade|local={{ delay: 125, duration: 50 }}
				animate:flip={{ duration: 150 }}
			>
				<div class="msginfo">
					<span class="username"
						>{shortenAuthor(comment.author, "")}</span
					>
					<span class="datetime"
						>{formatDateTime(comment.senttime)}</span
					>
				</div>
				<div class="msg">
					{@html comment.content}
					<div
						class={comment.read ? "doublecheckmark" : "checkmark"}
					/>
				</div>
			</article>
		{/each}
	</div>
	<span
		class="textarea"
		role="textbox"
		contenteditable
		on:keyup={handleKeydown}
	/>
</div>

<style>
	/* unread message count */
	.badge {
		display: inline-block;
		position: absolute;
		top: 0;
		background-color: #4285f4;
		color: #d7e6fd;
		right: 0.8rem;
		border-radius: 9999px;
		font-size: 0.9em;
		min-width: 1.2rem;
		line-height: 1.5em;
		min-height: 1rem;
		text-align: center;
	}

	.blinking {
		background: var(--blinkingstate);
		animation: pulse-red 1.25s 120;
	}

	@keyframes pulse-red {
		0% {
			/* transform: scale(0.95); */
			box-shadow: 0 0 0 0 rgba(255, 82, 82, 0.7);
		}

		70% {
			transform: scale(1);
			box-shadow: 0 0 0 16px rgba(255, 82, 82, 0);
		}

		100% {
			/* transform: scale(0.95); */
			box-shadow: 0 0 0 0 rgba(255, 82, 82, 0);
		}
	}

	.notifyBtn {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url("assets/svg/chat.svg") no-repeat;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
	}

	.notifyBtnclose {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url("assets/svg/close.svg") no-repeat;
		fill: white;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
	}

	.notifyBtn:active {
		border: none;
	}

	/* .closeBtn {
		position: fixed;
		right: 0;
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		border: none;
		cursor: default;
		overflow: hidden;
		outline: none;
		background: transparent;
		z-index: -1;
	} */

	/* chat styling */
	.chat {
		direction: rtl;
		display: flex;
		flex-direction: column;
		max-width: 42ch;
		width: 20vw;
		margin-left: auto;
		margin-right: auto;
		min-width: 24ch;
		z-index: 25;
		position: absolute;
		right: 1rem;
		resize: both;
		overflow: auto;
		border: 1px solid var(--basharqred);
		background: var(--pagebackground);
		box-shadow: 0px 16px 20px 0px rgba(0, 0, 0, 0.19);
		margin-top: 2rem;
		border-radius: 0px 15px 0px 15px;
	}

	/**
	 * WRAPPER
     * Sets a max width, adds a consistent gutter and horizontally
     * centers the contents
     */
	/* .wrapper {
		width: 90vw;
		width: clamp(16rem, 90vw, 70rem);
		margin-left: auto;
		margin-right: auto;
		padding-left: 1.5rem;
		padding-right: 1.5rem;
		position: relative;
	} */

	.scrollable {
		height: 35vh;
		width: auto;
		padding: 1em 0 1em 0;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		flex-grow: 1;
		margin: 0.7em;
		overflow-y: auto;
		overflow-x: hidden;
		overflow-wrap: break-word;
	}

	article {
		margin-bottom: 1em;
		color: #333;
		width: 80%;
	}

	article:first-child {
		margin-top: auto;
	}

	header {
		display: flex;
		flex-flow: row wrap;
		align-items: flex-start;
		margin: 0.7em;
		padding: 0.5em;
		background: white;
		border: 1px solid lightgrey;
		border-radius: 0px 15px 0px 15px;

		max-height: 5em;
		min-height: 3.2em;
		overflow-y: auto;
	}

	.username {
		direction: ltr;
		text-align: left;
		font-size: 12px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		background-color: white;
		padding: 0.35em 0.7em;
		width: max-content;
		/* border: 0.5px solid rgb(196, 27, 26, 0.3); */
		border-radius: 0px 10px 0px 10px;
	}

	.datetime {
		font-size: 10px;
		font-weight: 700;
		color: grey;
		margin-right: 12px;
	}

	.username:hover {
		white-space: normal;
		overflow: visible;
	}

	.msg {
		text-align: right;
		font-size: 1em;
		flex-grow: 1;
		padding: 0.5em;
		background-color: var(--greyish);
		border-radius: 0px 15px 0px 15px;
	}

	.checkmark {
		display: inline;
		background: url("assets/svg/checkmark.svg") no-repeat;
		width: 12px;
		height: 12px;
		position: relative;
		float: left;
		margin-top: 0.8em;
		z-index: 10;
		background-size: 100%;
		cursor: pointer;
	}

	.doublecheckmark {
		display: inline;
		background: url("assets/svg/doublecheckmark.svg") no-repeat;
		width: 12px;
		height: 12px;
		position: relative;
		float: left;
		margin-top: 0.8em;
		z-index: 10;
		background-size: 100%;
		cursor: pointer;
	}

	.msginfo {
		text-align: right;
	}

	.other {
		margin-right: auto;
	}
	.hidden {
		visibility: hidden;
	}

	/* Works on Firefox */
	* {
		scrollbar-width: thin;
		scrollbar-color: var(--scrollbarred) lightgrey;
	}

	/* Works on Chrome, Edge, and Safari */
	*::-webkit-scrollbar {
		width: 6px;
		height: 6px;
	}

	*::-webkit-scrollbar-track {
		background: #f6f6f6;
	}

	*::-webkit-scrollbar-thumb {
		background-color: var(--scrollbarred);
		border-radius: 20px;
		border: 1px solid var(--scrollbarred);
		border-radius: 0 3px 0 3px;
	}

	.textarea {
		font-family: "Bloomberg Arabic Beta v4", -apple-system,
			BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
			"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
		display: block;
		width: auto;
		color: #333;
		text-align: right;
		overflow: hidden;
		resize: vertical;
		min-height: 1ch;
		background-color: white;
		font-size: 100%;
		padding: 0.5em;
		margin: 0.5em;
		outline: none;
		border: 1px solid lightgrey;
		border-radius: 0px 15px 0px 0px;
	}

	.textarea:focus {
		border: 1px dotted var(--basharqred);
	}
</style>
