<script>
  import { createEventDispatcher } from "svelte";
  import { currentMsgId } from "../store.js";
  import { formatTitle } from "./Formatitle.ts";
  import File from "./File.svelte";

  export let id;
  export let expanded;
  export let text;
  export let messages;

  const dispatch = createEventDispatcher();

  function toggle() {
    expanded = !expanded;
  }

  function copyBNData() {
    currentMsgId.set(id);
    expanded = true;
  }
</script>

<style>
  .icon {
    padding: 0 0 0 28px;
    padding-right: 15px;
    margin: 0 0 0 0.35rem;
    background: url('assets/svg/folder.svg') right 20px center  no-repeat;
    background-size: 20px 17px;
    font-weight: bold;
    cursor: pointer;
    position: relative;
  }

  .icon:after {
    content: '';
    position: absolute;
    right: 0;
    top: 10px;
    width: 10px;
    height: 6px;
    background: url('assets/svg/arrow.svg')no-repeat center;
    background-size: 100%;
    transform: rotate(180deg);
    transition-duration: .2s;
  }

  .icon.expanded:after{
    transform: rotate(0deg);
    transition-duration: .5s;
  }

  .foldername {
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    padding: 0.1rem 0.4rem 0.05rem 0.4rem;
    direction: initial;
    text-align: right;
  }

  .foldername:hover {
    font-weight: bold;
    cursor: pointer;
    color: var(--basharqred);
    padding: 0.1rem 0.4rem 0.05rem 0.4rem;
  }

  .expanded {
    background-image: url(assets/svg/folder-open.svg);
  }

  .expanded ~ .foldername{
      color: var(--basharqred)!important;
  }

  ul {
    padding: 0.2em 0 0 0.5em;
    margin: 0 1rem 0 0;
    margin-bottom: 15px;
    list-style: none;
    max-width: 355px;
    font-size: 1.0rem;
  }

  li {
    margin: 5px 0 5px 0;
    padding: 0.2rem 0;
    color: #333;
  }

  .dir {
    display: flex;
    color: #333;
  }

</style>

<div class="dir">
  <span class="icon" class:expanded on:click={toggle} />
  <span class="foldername" on:dblclick={copyBNData}>{formatTitle(text)}</span>
</div>

{#if expanded}
  <ul>
    {#each messages as message}
      <li>
        {#if message.messages}
          <svelte:self id={message.id} text={message.text} messages={message.messages} expanded/>
        {:else}
          <File text={message.text} author={message.author} lastPlayed={message.aired}/>
        {/if}
      </li>
    {/each}
  </ul>
{/if}
