<script>
	import { fontSizeStore } from "../store";
	import { createEventDispatcher } from "svelte";

	const dispatch = createEventDispatcher();

	// function convertRemToPixels(rem) {
	//     return (
	//         rem *
	//         parseFloat(getComputedStyle(document.documentElement).fontSize)
	//     );
	// }

	function increment() {
		fontSizeStore.update((n) => Math.min(2.5, n + 0.25));
		dispatch("fontsize", { fontsize: $fontSizeStore });
	}

	function decrement() {
		fontSizeStore.update((n) => Math.max(n - 0.25, 0.75));
		dispatch("fontsize", { fontsize: $fontSizeStore });
	}

	function reset() {
		fontSizeStore.set(1.25);
		dispatch("fontsize", { fontsize: $fontSizeStore });
	}
</script>

<div class="wrapper">
	<button
		class="fontdecrease"
		on:click={decrement}
		style="width: 22px; height:22px"
	/>
	<button class="revert" on:click={reset} style="width: 22px; height:22px" />
	<button
		class="fontincrease"
		on:click={increment}
		style="width: 28px; height:28px"
	/>
</div>

<style>
	.wrapper {
		display: flex;
		align-items: baseline;
	}

	button:hover {
		opacity: 1;
	}

	.revert {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url("assets/svg/undo.svg") no-repeat;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
		opacity: 0.6;
		transition: opacity 0.1s;
	}

	.fontdecrease {
		width: 28px;
		height: 28px;
		position: relative;
		z-index: 10;
		background: url("assets/svg/fontdecrease.svg") no-repeat;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
		opacity: 0.6;
		transition: opacity 0.1s;
	}

	.fontincrease {
		width: 28px;
		height: 28px;
		top: 0.16em;
		position: relative;
		z-index: 10;
		background: url("assets/svg/fontincrease.svg") no-repeat;
		background-size: 100%;
		cursor: pointer;
		border: none;
		border-radius: 50%;
		justify-content: right;
		opacity: 0.6;
		transition: opacity 0.1s;
	}
</style>
