<script>
  import * as config from "./config.prod";

  import { onMount, beforeUpdate, afterUpdate, tick } from "svelte";
  import { fade } from "svelte/transition";

  import store2 from "store2";

  import Content from "./components/Content.svelte";
  import Modal from "svelte-simple-modal";

  import Fab from "./components/Fab.svelte";
  import Ainput from "./components/Ainput.svelte";
  import TitleInput from "./components/Titleinput.svelte";
  import Folder from "./components/Folder.svelte";
  import DragDrop from "./components/DragAndDropList.svelte";
  import PlayButton from "./components/PlayButton.svelte";
  import StopButton from "./components/StopButton.svelte";
  import AutoPlayButton from "./components/AutoPlayButton.svelte";
  import AutoStopButton from "./components/AutoStopButton.svelte";
  import Playnotification from "./components/Playnotification.svelte";
  import FontSize from "./components/FontSize.svelte";
  import Connection from "./components/Connection.svelte";
  import { Stretch } from "svelte-loading-spinners";
  import * as animateScroll from "svelte-scrollto";

  import Queue from "smart-request-balancer";

  import {
    playButton,
    stopButton,
    autoPlayButton,
    autoStopButton,
  } from "./components/buttondefinitions.js";

  import shortenAuthor from "./components/ShortenAuthor";

  import SvelteTooltip from "./components/SvelteTooltip.svelte";

  import Timer from "./components/TimerCss.svelte";

  import Messages from "./components/notifications/Messages.svelte";
  import Chat from "./components/chat/Chat.svelte";
  import { center } from "./components/notifications/notifications";

  import { createEmptyBNMsg, createEmptyLine } from "./templateMsg.js";
  import { currentMsgId, templateStore, fontSizeStore } from "./store.js";

  import { AudioTimer } from "./delay";

  import axios from "axios";

  import Primus from "./primus-client";
  import emit from "primus-emit";

  import {
    UnexpectedError,
    NoDataSourceError,
    CouchDbSaveError,
    NetworkError,
  } from "./errors";

  import { mockData, mockTemplates } from "./mockdata";
  import CouchDb from "./couchdb.js";
  import debounce from "./utils";
  import { filterJson } from "./search.js";

  import { getCookiebyName, getCookieValue, resetCookie } from "./cookie";
  import constants from "./constants";
  import { LogMessage } from "./logmessage.js";
  import { LogLevel, UserState } from "./enums.js";
  import { notifications } from "./notifications.js";

  import deepClone from "./deepclone.js";

  import { playSound } from "./sounds";

  import Vizrtpreview from "./components/preview/Vizrtpreview.svelte";

  import { chatmessages, users } from "./components/chat/stores";
  import { nsResolver } from "./components/preview/vizrtpreview";

  // mock data or a database
  let db;
  const couchdbData = function (url, user, pass, db) {
    return new CouchDb(url, user, pass, db);
  };

  // unmarshal "undefined" to undefined
  (function (_) {
    _.fn("getu", function (key) {
      return this.get(key) === "undefined" ? undefined : this.get(key);
    });
  })(store2._);

  let couchdbEvents;

  // state of the play and stop button
  let play,
    autoplay,
    stopping = false,
    autostopping = false;

  // timer functions
  let TimerComponent;

  let timerStart;
  let timerStop;
  let timerRestart;

  let root = [];
  let searchPhrase = "";
  templateStore.set([]);

  let selectedTemplate = null;

  let duration = 7;
  let loop = 1;

  let dialog;

  // font size
  const fs = store2.get(constants.FontSize);
  console.log("font size: ", fs);
  if (!fs) {
    fontSizeStore.set(1.25);
  } else {
    fontSizeStore.set(Math.max(store2.get(constants.FontSize), 0.75));
  }

  // for connection indicator
  let connected = true;

  // someone is playing
  let onair;
  let playoutNotifications;

  // WebAudio based timer
  let atimer;

  // request queue
  const queue = new Queue({
    rules: {
      playQueue: {
        // Rule for playout queue
        rate: 1, // one message
        limit: 3, // per second
        priority: 1,
      },
      stopQueue: {
        // Rule for playout queue
        rate: 1, // one message
        limit: 1, // per second
        priority: 1,
      },
      CouchDbQueue: {
        // Rule for saving data
        rate: 1, // 1 message
        limit: 2, // per second
        priority: 4,
      },
    },
  });

  function getBNData(ds) {
    switch (ds) {
      case "mock":
        const d = mockData();
        return d;
      case "couchdb":
        db = new CouchDb(
          config.CouchDb.Url,
          config.CouchDb.User,
          config.CouchDb.Password,
          config.CouchDb.Database
        );
        return db.GetData().then((d) => {
          return d;
        });
      default:
        throw new NoDataSourceError(`no such datasource "${ds}"`);
    }
  }

  // TODO extFeedUrl should be known here
  let vizPreview = {
    pdsUrl: config.PilotDataServerUrl,
    prvUrl: config.previewOptions.previewHostUrl,
    extFeedUrl: "",
  };

  let imgW;

  // an index of the text filed being edited
  let currEditedItemIndex = -1;

  $: selected = currentMessage.messages.reduce(
    (acc, currVal) => acc || currVal.selected,
    false
  );
  $: hidden = selected ? "" : "hidden";

  $: grouped = currentMessage.messages.reduce(
    (acc, currVal) => acc || currVal.grouped,
    false
  );

  // shows "rm from group" button if a selected line
  // is in group
  $: ingroup = currentMessage.messages.some(
    (m) => m.selected === true && m.grouped === true
  );

  $: autoplayenabled = grouped;

  // returns ISO formatted date string
  function dateNow() {
    const d = new Date();
    return d.toISOString();
  }

  let currentUser;

  // let's start with a blank message
  let currentMessage;

  function getMsg(id) {
    let m = root.find((m) => m.id === id);
    // create new if not found
    switch (m) {
      case undefined:
        // user stored state - selected template, but do not use the stored one
        // just retrieve a new (possibly updated) using the id
        if (!selectedTemplate) {
          selectedTemplate = store2.getu(constants.SelectedTemplate);
        }
        if (selectedTemplate && selectedTemplate.value) {
          selectedTemplate = $templateStore.find(
            (t) => t.value === selectedTemplate.value
          );
        }
        m = createEmptyBNMsg(selectedTemplate);
        root = [m].concat(root);
        currentMsgId.set(m.id);
        break;
      default:
        // replace template definition for each message
        // with a template from the store (might be updated)
        const defTemplate = $templateStore[1];
        m.messages.forEach((l) => {
          if (!l.template) l.template = m.template || defTemplate;
          const t = $templateStore.find(
            (t) => t.valueName === l.template.valueName
          );
          l.template = t ? t : l.template;
        });
        break;
    }
    return m;
  }

  // TODO: umarshall "undefined"
  // remember the choosen graphic template
  $: if (selectedTemplate !== null)
    store2.set(constants.SelectedTemplate, selectedTemplate);

  $: filteredRoot = filterJson(root, searchPhrase, "text");

  // (handle double-click event on the treeview)
  $: currentMessage = getMsg($currentMsgId);
  $: selectedTemplate = currentMessage.messages[currEditedItemIndex]
    ? currentMessage.messages[currEditedItemIndex].template
    : undefined;

  $: datatoRender = currentMessage.messages[currEditedItemIndex]
    ? currentMessage.messages[currEditedItemIndex]["text"]
    : "";

  // preview
  $: xyShift = selectedTemplate
    ? `object-position: ${selectedTemplate.hShift} ${selectedTemplate.vShift};`
    : "object-position: 0% 0%;";

  // async reactive declarations https://github.com/sveltejs/svelte/issues/2118
  // let filtered;
  // $: (async() => filtered = await getItems())();

  // handle emitting a message to server
  function handleEmitMessage(event) {
    if (couchdbEvents) {
      couchdbEvents.emit("chatmessage", event.detail);
    }
  }

  // add and remove messages
  function handleFabClick() {
    if (messageGroupExists(root, $currentMsgId)) {
      addEmptyLine();
    }
  }

  // standart selection operations with Ctrl and Shift Keys
  function handleClick(event) {
    switch (true) {
      case event.detail.meta.ctrlKey:
        selectToggleItem(event.detail.MessageId);
        break;
      case event.detail.meta.shiftKey:
        const currIdx = currentMessage.messages.findIndex(
          (item) => item.id === event.detail.MessageId
        );
        const above = findSelectedAbove(currentMessage.messages, currIdx);
        if (above < currIdx) {
          selectItems(above, currIdx);
        }
        const below = findSelectedBelow(currentMessage.messages, currIdx);
        if (below > currIdx) {
          selectItems(currIdx, below);
        }
        break;
      default:
        deselectAllBut(event.detail.MessageId);
        // selectToggleItem(event.detail.MessageId);
        break;
    }
  }

  function findSelectedAbove(array, idx) {
    for (var index = 0; index < idx; index++) {
      if (array[index].selected) break;
    }
    return index;
  }

  function findSelectedBelow(array, idx) {
    for (var index = array.length - 1; index > idx; index--) {
      if (array[index].selected) break;
    }
    return index;
  }

  function selectToggleItem(id) {
    const currEditedItemIdx = currentMessage.messages.findIndex(
      (item) => item.id === id
    );
    currentMessage.messages[currEditedItemIdx].selected = !currentMessage
      .messages[currEditedItemIdx].selected;
  }

  function selectItem(id) {
    currEditedItemIndex = currentMessage.messages.findIndex(
      (item) => item.id === id
    );
    markItemAsSelected(currEditedItemIndex);
  }

  function selectItems(from, to) {
    for (let index = from; index <= to; index++) {
      markItemAsSelected(index);
    }
  }

  function handleDeselectAllClick() {
    deselectAll();
  }

  function deselectAllBut(id) {
    currentMessage.messages.forEach((m) =>
      m.id !== id ? (m.selected = false) : (m.selected = !m.selected)
    );
    currentMessage.messages = currentMessage.messages;
  }

  // function handleDelete(event) {
  //   currentMessage.messages = currentMessage.messages.filter(
  //     (m) => m.id !== event.detail.MessageId
  //   );
  //   handleSave();
  //   root = root;
  // }

  // function handleMarkDelete(event) {
  //   currentMessage.messages.find((m) => {
  //     if (m.id === event.detail.MessageId) m.deleted = event.detail.State;
  //   });
  //   selected = currentMessage.messages.reduce(
  //     (acc, currVal) => acc || currVal.selected,
  //     false
  //   );
  // }

  function handleDeleteMarked() {
    currentMessage.messages = currentMessage.messages.filter(
      (m) => !m.selected || m.onair
    );
    selected = false;
    handleSave().catch((err) => console.log(err));
  }

  // find the currently bound item to send proper value to preview
  // in playout mode, mark the item as cued
  function handleFocus(event) {
    currEditedItemIndex = currentMessage.messages.findIndex(
      (item) => item.id === event.detail.MessageId
    );

    currentMessage.messages.forEach((m) => {
      m.selected = false;
    });
    if (autoplay) {
      if (currentMessage.messages[currEditedItemIndex].grouped) {
        markGroupedItemAsCued(currEditedItemIndex);
      }
    }

    if (play) {
      markItemAsCued(currEditedItemIndex);
    }
  }

  function handleFontSize(event) {
    store2.set(constants.FontSize, event.detail.fontsize);
  }

  // add empty line:
  // Ctrl+Enter - below the current
  // Shift+Enter - above the current
  const handleKeyDown = (e) => {
    switch (e.keyCode) {
      case 13:
      case 10:
        if (e.ctrlKey) {
          if (messageGroupExists(root, $currentMsgId)) {
            addEmptyLine(currEditedItemIndex + 1);
          }
        }
        if (e.shiftKey) {
          if (messageGroupExists(root, $currentMsgId)) {
            addEmptyLine(currEditedItemIndex);
          }
        }
        break;
      default:
        break;
    }
    currentMessage = currentMessage;
  };

  function markGroupedItemAsCued(idx) {
    const cm = currentMessage.messages[idx];
    currentMessage.messages.forEach((m) => {
      if (m.grouped) {
        delete m["cued"];
      }
    });
    cm.cued = true;
    currentMessage.messages = currentMessage.messages;
  }

  function markItemAsCued(idx) {
    const cm = currentMessage.messages[idx];
    if (!autoplay) {
      currentMessage.messages.forEach((m) => {
        delete m["cued"];
      });
    } else {
      currentMessage.messages.forEach((m) => {
        if (!m.grouped) {
          delete m["cued"];
        }
      });
    }
    cm.cued = true;
    currentMessage.messages = currentMessage.messages;
  }

  function markItemAsSelected(idx) {
    currentMessage.messages[idx].selected = true;
  }

  function handleTemplateSelected(event) {
    const currEditedItemIdx = currentMessage.messages.findIndex(
      (item) => item.id === event.detail.MessageId
    );
    currentMessage.messages[currEditedItemIdx].template = event.detail.template;
    if (
      currentMessage.messages.length > 1 ||
      currentMessage.messages[0].text !== ""
    ) {
    }
  }

  // add to group - all the selected messages - in case a group
  // already exists, shift selected lines to the end of group
  function handleGroup() {
    let lastGroupIndex = -1;
    currentMessage.messages.forEach((m, index) => {
      if (m.selected && !m.grouped) {
        const tmp = currentMessage.messages.splice(index, 1)[0];
        tmp.grouped = true;
        m.selected = false;
        lastGroupIndex = findLastInGroup();
        currentMessage.messages.splice(lastGroupIndex + 1, 0, tmp);
      }
    });
    currentMessage.messages = currentMessage.messages;
  }

  function handleRemoveFromGroup() {
    let tmp = [];
    for (var i = 0; i < currentMessage.messages.length; i++) {
      if (
        currentMessage.messages[i].selected &&
        currentMessage.messages[i].grouped &&
        !currentMessage.messages[i].onair
      ) {
        const item = currentMessage.messages.splice(i, 1)[0];
        item.selected = item.grouped = item.cued = false;
        tmp.push(item);
        --i;
      }
    }
    const lastGroupIndex = findLastInGroup();
    if (lastGroupIndex < 0) {
      lastGroupIndex = currentMessage.messages.length - 1;
    }
    currentMessage.messages.splice(lastGroupIndex + 1, 0, ...tmp);
    currentMessage.messages = currentMessage.messages;
  }

  // function findFirstInGroup() {
  //   let firstGroupIndex = -1;
  //   for (let idx = 0; idx < currentMessage.messages.length; idx++) {
  //     if (currentMessage.messages[idx].grouped) {
  //       firstGroupIndex = idx;
  //       break;
  //     }
  //   }
  //   return firstGroupIndex;
  // }

  function findLastInGroup() {
    let lastGroupIndex = -1;
    let idx = currentMessage.messages.length - 1;
    for (; idx >= 0; idx--) {
      if (currentMessage.messages[idx].grouped) {
        lastGroupIndex = idx;
        break;
      }
    }
    return lastGroupIndex;
  }

  function handleUngroup() {
    if (!autoplay) {
      currentMessage.messages.forEach((m) => delete m.grouped);
      currentMessage.messages = currentMessage.messages;
    }
  }

  // refresh preview
  const handleKeyUp = debounce((e) => {
    switch (e.keyCode) {
      default:
        const currLine = currentMessage.messages[currEditedItemIndex];
        if (currLine.text !== e.target.value) {
          // update the 'author' field, as the line can be created by someone else
          currLine.text = e.target.value;
          currLine.author = currentUser;
          currentMessage.author = currentUser;
          // add a new message group if not found
          if (!messageGroupExists(root, $currentMsgId)) {
            root.unshift(currentMessage);
          }
          root = root;
          // saving current message
          handleSave().catch((err) => console.log(err));
        }
        break;
    }
  }, 350);

  async function handleTitleChange(event) {
    await tick();
    currentMessage.text = event.detail.title;
    if (!messageGroupExists(root, currentMessage.id)) {
      root.unshift(currentMessage);
    }
    handleSave();
  }

  function addEmptyLine(idx) {
    const el = createEmptyLine(currentMessage.messages.slice(-1));
    if (idx === undefined) {
      currentMessage.messages = [...currentMessage.messages, el];
    } else {
      currentMessage.messages.splice(idx, 0, el);
    }
    animateScroll.scrollToBottom({
      container: "#messages",
      offset: 280,
      duration: 250,
    });
  }

  function messageGroupExists(arr, id) {
    return arr.findIndex((e) => e.id === id) > -1;
  }

  function handleAutoPlay() {
    if (
      !autoplay &&
      selectedTemplate &&
      currentMessage.messages.some((t) => t.grouped)
    ) {
      dialog.toggle();
    }
  }

  function handleOKautoPlay(event) {
    autoplay = true;
    store2.set("AutoMsgDuration", event.detail.duration);
    autoPlay(currEditedItemIndex, event.detail.duration + 1);
  }

  // autoplay - for grouped messages
  function autoPlay(index, duration) {
    handlePlaySilent(index);
    atimer = new AudioTimer();
    index = cueNextInGroup(index, currentMessage);
    markGroupedItemAsCued(index);
    atimer.Cue(duration).then(() => {
      if (index < currentMessage.messages.length && autoplay) {
        atimer = {};
        // maybe it was recued manually?
        index = currentMessage.messages.findIndex((m) => m.cued && m.grouped);
        if (index < 0) {
          index = cueNextInGroup(index, currentMessage);
        }
        autoPlay(index, duration);
      }
    });
  }

  // version of play for grouped messages
  function handlePlaySilent(index) {
    // ugly hack to restart a timer
    timerRestart();
    setTimeout(() => {
      timerStart();
    }, 100);

    if (config.dataSendUrl) {
      // assign the cued message's template to the top level template
      //  (for compatibility with a backend)
      updateOnAirTime(currentMessage, index);
      setOnAir(currentMessage, index);

      let data = addMetaData(deepClone(currentMessage));
      data.template = data.messages[index].template;

      // loop, duration, etc.
      // leave only one 'onair' property for proper playout
      data = filterOtherMessages(data, index);
      const key = data.id;
      const rule = "playQueue";
      queue.request(
        () =>
          axios
            .put(config.dataSendUrl, data, {
              timeout: config.fetchTimeout,
              headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
              },
            })
            .then((response) => {
              if (response.status && response.status >= 400) {
                play = false;
                throw new UnexpectedError(response.statusText);
              }
              console.log("response.data on (auto) play: ", response.data);

              // inform others
              couchdbEvents.emit("takein", { user: currentUser });

              const results = response.data;
              handleOnAirResults(results.path_onair);
              handleWebsiteResults(results.path_web);
              handleTwitterResults(results.path_twitter);

              // handleSave();
              currentMessage = currentMessage;
            })
            .catch((error) => {
              const err = formatError(error);
              console.error("play error:", err);
              addNotification(
                notifications.PlayError(error.message),
                LogLevel.Error
              );
              // autoplay = false;
            }),
        key,
        rule
      );
    }
  }

  // manual playout
  function handlePlayClick(configObj) {
    // guard against playout with no template selected
    if (!selectedTemplate) {
      return;
    }

    let cued = -1;
    // find cued element
    if (autoplay) {
      cued = currentMessage.messages.findIndex((m) => m.cued && !m.grouped);
    } else {
      cued = currentMessage.messages.findIndex((m) => m.cued);
    }
    if (cued == -1) {
      cued = configObj.currEditedItemIndex;
    }
    // }
    // TODO this is strange
    if (cued < 0) {
      return;
    }

    play = true;
    // ugly hack to restart a timer
    timerRestart();
    setTimeout(() => {
      timerStart();
    }, 50);

    if (config.dataSendUrl) {
      console.log("dataSendUrl: ", config.dataSendUrl);
      // assign the cued message's template to the top level template
      //  (for compatibility with a backend)
      currentMessage.template = currentMessage.messages[cued].template;

      updateOnAirTime(currentMessage, cued);
      setOnAir(currentMessage, cued);

      currEditedItemIndex = cued;
      // refresh GUI
      currentMessage = currentMessage;

      // loop, duration, etc.
      // leave only one 'onair' property for proper playout
      let data = addMetaData(deepClone(currentMessage));
      data = filterOtherMessages(data, cued);

      const key = data.id;
      const rule = "playQueue";
      queue
        .request(
          () =>
            axios
              .put(config.dataSendUrl, data, {
                timeout: config.fetchTimeout,
                headers: {
                  "Content-Type": "application/json",
                  Accept: "application/json",
                },
              })
              .then((response) => {
                if (response.status && response.status >= 400) {
                  play = false;
                  throw new UnexpectedError(response.statusText);
                }
                // inform others
                couchdbEvents.emit("takein", { user: currentUser });

                cued = cueNextByTemplateId(
                  cued,
                  currentMessage,
                  selectedTemplate.valueName
                );
                // }
                markItemAsCued(cued);

                // handleSave();
                currentMessage = currentMessage;
                return response.data;
              })
              .catch((error) => {
                const err = formatError(error);
                console.error("play error:", err);
                addNotification(
                  notifications.PlayError(error.message),
                  LogLevel.Error
                );
                // play = false;
              }),
          key,
          rule
        )
        .then((results) => {
          console.log("response data on (manual) play: ", results);
          handleOnAirResults(results.path_onair);
          handleWebsiteResults(results.path_web);
          handleTwitterResults(results.path_twitter);
        })
        .catch((error) => console.error(error));
    }
  }

  function handleStopClick() {
    if (stopping) {
      return;
    }
    // do not stop autoplayed items
    if (autoplay && !play) {
      return;
    }
    // find the line to take off
    let offAirIndex = -1;
    offAirIndex = currEditedItemIndex;

    // if only one an-air
    const onairMsg = currentMessage.messages.filter(
      (m) => m.onair && !m.grouped
    );
    if (onairMsg.length === 1) {
      offAirIndex = currentMessage.messages.findIndex(
        (m) => m.id === onairMsg[0].id
      );
      // uncue all
      currentMessage.messages.forEach((m) => (m.cued = false));
    }
    if (offAirIndex === -1) {
      return;
    }

    if (offAirIndex >= 0) {
      stopping = true;
      stopCurrentItem(currentMessage, offAirIndex).then((results) => {
        console.log("stop (manual) response: ", results);
        if (results) {
          handleOnAirResults(results.path_onair);
          handleWebsiteResults(results.path_web);
          handleTwitterResults(results.path_twitter);
        }
        setOffAir(currentMessage, offAirIndex);
        timerStop();

        currentMessage.messages.forEach((m) => (m.protected = false));
        currentMessage = currentMessage;
        couchdbEvents.emit("takeout", { user: currentUser });

        // save to db
        handleSave().catch((err) => console.log(err));

        // keep the "play" button pulsing, there is still sth on-air
        if (!currentMessage.messages.some((m) => m.onair && !m.grouped)) {
          console.log("still a manual one on air");
          play = false;
        }

        setTimeout(function () {
          timerRestart();
        }, 500);
      });
      stopping = false;
    }
  }

  function handleAutoStop() {
    if (autostopping) {
      return;
    }
    let idx = currentMessage.messages.findIndex((m) => m.grouped && m.onair);
    if (idx < 0) {
      return;
    }
    autoplay = false;
    autostopping = true;
    stopCurrentItem(currentMessage, idx).then((results) => {
      console.log("stop (auto) response: ", results);
      if (results) {
        handleOnAirResults(results.path_onair);
        handleWebsiteResults(results.path_web);
        handleTwitterResults(results.path_twitter);
      }
      setOffAir(currentMessage, idx);
      timerStop();

      autoplay = false;
      autostopping = false;
      // save to db
      handleSave().catch((err) => console.log(err));
      currentMessage = currentMessage;
      couchdbEvents.emit("takeout", { user: currentUser });
    });
  }

  async function stopCurrentItem(currentMessage, index) {
    // prepare data to be sent to backend
    currentMessage = addMetaData(currentMessage);
    let data = deepClone(currentMessage);
    data = filterOtherMessages(data, index);
    data.template = data.messages[0].template;

    const key = data.messages[0].id;
    const rule = "stopQueue";
    const response = await queue.request(
      () =>
        axios
          .put(config.dataStopUrl, data, {
            timeout: config.fetchTimeout,
            headers: {
              "Content-Type": "application/json",
            },
          })
          .then((response) => {
            const results = response.data;
            if (response.status && response.status !== 200) {
              console.log("stop: ", response);
              throw new NetworkError(response.statusText);
            }
            return results;
          })
          .catch((error) => {
            const err = formatError(error);
            console.error("stop operation problem:", error);
            addNotification(
              notifications.StopError(error.message),
              LogLevel.Error
            );
          }),
      key,
      rule
    );
    return response;
  }

  // results of the play/stop operations
  function handleOnAirResults(res) {
    // one or more?
    if (!Array.isArray(res)) {
      res = [res];
    }

    res.forEach((r) => {
      if (!r.success) {
        switch (r.status) {
          case 410:
            addNotification(
              notifications.PlayError(
                r.message !== "" ? r.message : `${r.statusCode} - ${r.url}`
              ),
              LogLevel.Warn
            );
            break;
          case 404:
            addNotification(
              notifications.PlayError(
                r.message !== "" ? r.message : `${r.statusCode} - ${r.url}`
              ),
              LogLevel.Error
            );
            break;
          default:
            addNotification(
              notifications.PlayError(
                r.message !== "" ? r.message : `${r.statusCode} - ${r.url}`
              ),
              LogLevel.Error
            );
            break;
        }
      }
    });
  }

  function handleWebsiteResults(res) {
    if (!Array.isArray(res)) {
      res = [res];
    }

    res.forEach((r) => {
      if (!r.success) {
        addNotification(
          notifications.WebsiteError(`web: ${r.message}`),
          LogLevel.Error
        );
      }
    });
  }

  function handleTwitterResults(res) {
    if (!res.success) {
      switch (res.message) {
        case "Status is a duplicate.":
          break;
        default:
          addNotification(
            notifications.TwitterError(`${res.message}`),
            LogLevel.Error
          );
          break;
      }
    }
  }

  function formatError(error) {
    console.dir(error, { depth: null });
    return error.response
      ? `${error.message}, ${JSON.stringify(error.response.data)}`
      : error.message;
  }

  function filterOtherMessages(message, index) {
    const currMessage = message.messages[index];
    message.messages.length = 0;
    message.messages.push(currMessage);
    return message;
  }

  // Finds next item of the same template
  function cueNextByTemplateId(cued, msg, tId) {
    do {
      cued = ++cued % msg.messages.length;
    } while (
      msg.messages[cued].template &&
      msg.messages[cued].template.valueName !== tId
    );
    return cued;
  }

  function cueNextInGroup(index, msg) {
    // cued manually
    const cued = msg.messages.findIndex((m) => m.cued && m.grouped);
    if (cued > -1) {
      return cued;
    }
    // find next in the group
    do {
      index = ++index % msg.messages.length;
    } while (!msg.messages[index].grouped);
    return index;
  }

  function setOnAir(currentMessage, currEditedItemIndex) {
    let currentLine;
    currentMessage = clearStatusPropsForCurrentTemplate(
      currentMessage,
      currEditedItemIndex
    );
    if (currEditedItemIndex > -1) {
      currentLine = currentMessage.messages[currEditedItemIndex];
      currentLine.onair = true;
      return currentLine;
    }
  }

  function setOffAir(currentMessage, idx) {
    clearStatusPropsForCurrentTemplate(currentMessage, idx);
    currEditedItemIndex = -1;
  }

  // clears 'onair' and 'cued' properties for messages wih the template
  // on the same layer
  function clearStatusPropsForCurrentTemplate(cm, cueIndex) {
    if (cueIndex > -1) {
      const currTemplateLayerId = cm.messages[cueIndex].template
        ? cm.messages[cueIndex].template.layer
        : undefined;
      cm.messages.forEach((m) => {
        if (m.template && m.template.layer === currTemplateLayerId) {
          delete m["onair"];
          delete m["cued"];
        }
      });
      return cm;
    }
  }

  function updateOnAirTime(msg, i) {
    if (i < msg.messages.length) {
      msg.messages[i].aired.push(dateNow());
    }
  }

  function addMetaData(doc) {
    doc.template = selectedTemplate;
    doc.duration = duration;
    doc.loop = loop;
    doc.author = currentUser;
    return doc;
  }

  // before saving, handle reordering in case a group exists -
  // add/remove from autoplay group
  function handleEndDrop() {
    handleSave().catch((err) => console.log(err));
  }

  async function handleSave() {
    return new Promise((resolve, reject) => {
      db.SaveDoc(currentMessage)
        .then(resolve())
        .catch((err) => {
          switch (true) {
            case err instanceof CouchDbSaveError:
              if (err.code === 409) {
                console.log("conflict: ", err);
                console.log("current revision: ", currentMessage._rev);
                db.GetDoc(db.getDoc(currentMessage._id))
                  .then((data) => {
                    currentMessage._rev = data._rev;
                    console.log("new revision: ", currentMessage._rev);
                  })
                  .catch((err) =>
                    console.log("failed to resolve conflict", err)
                  );
              }
              break;
            default:
              console.log("error while saving to db: ", err);
          }
          reject(err);
        });
    });
  }

  // db.SaveDoc(currentMessage)
  //   .then((root = root))
  //   .catch((err) => {
  //     switch (true) {
  //       case err instanceof CouchDbSaveError:
  //         console.error(err);
  //         // conflict
  //         if ((err.code = 409)) {
  //           console.log("error 409, conflict: ", err);
  //           // try to resolve
  //           console.log("current revision: ", currentMessage._rev);
  //           db.getDoc(currentMessage._id)
  //             .then((data) => {
  //               currentMessage._rev = data._rev;
  //               console.log("new revision: ", currentMessage._rev);
  //             })
  //             .catch((err) => console.log("failed to resolve conflict", err));
  //         }
  //         break;
  //       default:
  //         console.error(err);
  //     }
  //   });
  // }

  function updateDataFromOtherSession(data) {
    // is it a new story (message group)?
    const idx = root.findIndex((m) => m.id === data.id);
    // sth found
    if (idx > -1) {
      if (data.deleted) {
        root.splice(idx, 1);
      } else {
        root[idx] = data.doc;
      }
    } else {
      root.unshift(data.doc);
    }
    return root;
  }

  function addNotification(msg, lvl) {
    center.add(new LogMessage(msg, lvl).FormattedLogMessage);
  }

  onMount(async () => {
    try {
      window.onunhandledrejection = (e) => {
        console.log("window.onunhandledrejection: ", e.reason);
        e.preventDefault();
      };

      const ds = config.DataSource ? config.DataSource : "mock";
      switch (ds) {
        case "mock":
          root = mockData();
          $templateStore.set(mockTemplates() || []);
          break;
        case "couchdb":
          db = new CouchDb(
            config.CouchDb.Url,
            config.CouchDb.User,
            config.CouchDb.Password,
            config.CouchDb.Database
          );
          root = await db.GetData();

          // get templates
          let dbCfg = new CouchDb(
            config.CouchDbSettings.Url,
            config.CouchDbSettings.User,
            config.CouchDbSettings.Password,
            config.CouchDbSettings.Database
          );
          const templates = await dbCfg.GetTemplates();
          templateStore.set(templates);

          selectedTemplate = store2.getu(constants.SelectedTemplate);

          // timer stuff
          timerStart = function () {
            TimerComponent.TimerStart();
          };

          timerStop = function () {
            TimerComponent.TimerStop();
          };

          timerRestart = function () {
            TimerComponent.TimerRestart();
          };

          //
          // username, from the backend we receive "domain/username" (backslash is replaced with a forward slash)
          //
          // console.log("cookie name: ", constants.UserNameHeader);
          // console.log("cookie: ", getCookiebyName(constants.UserNameHeader));
          currentUser =
            decodeURIComponent(getCookiebyName(constants.UserNameHeader)) ===
            null
              ? ""
              : decodeURIComponent(getCookiebyName(constants.UserNameHeader));
          // resetCookie(constants.UserNameHeader);
          // currentUser = "CORP/John.Doe";
          console.log(`user: ${currentUser}`);

          couchdbEvents = new Primus();
          couchdbEvents.plugin("emit", emit);

          couchdbEvents.on("update", (incoming) => {
            console.log("update from db");
            let data = JSON.parse(incoming);
            if (data.id != currentMessage.id) {
              root = updateDataFromOtherSession(data);
            } else {
              currentMessage["_rev"] = data.doc["_rev"];
              // guard against notification of own edits
              console.log(
                `Current user: ${currentUser}, incoming: ${data.doc.author}`
              );
              if (data.doc.author && data.doc.author !== currentUser) {
                playSound("negative");
                addNotification(
                  notifications.SimultaneousEditing(data.doc.author),
                  LogLevel.Warn
                );
              }
            }
          });

          couchdbEvents.on("version", (data) => {
            console.log("version: ", data);
          });

          couchdbEvents.on("open", function open() {
            console.log("Connection to server is alive");
            connected = true;
            console.log("emitting current user: ", {
              username: currentUser || "",
            });
            this.emit("user", { username: currentUser || "" });
          });

          couchdbEvents.on("couchdb_error", (error) => {
            console.log("Something horrible has happened", error.stack);
          });

          couchdbEvents.on("reconnect", function (opts) {
            connected = false;
            console.log("Reconnection attempt started");
          });

          // a list of users from the server
          couchdbEvents.on("userlist", function (userlist) {
            $users = [];
            console.log("userlist: ", userlist);
            console.log("adding user from server: ", userlist);
            $users = [...$users, ...userlist];
          });

          // bulk of chat messages from the server
          couchdbEvents.on("chatinit", function (messages) {
            chatmessages.clear();
            chatmessages.addarr(messages);
          });

          // a new user has been registered
          couchdbEvents.on("userconnected", function (userdata) {
            console.log("user connected: ", userdata);
            userdata.active = UserState.active;
            const index = $users.findIndex((u) => u.id == userdata.id);
            if (index === -1) {
              $users = [...$users, userdata];
            }
          });

          couchdbEvents.on("usergone", function (userdata) {
            console.log("user gone: ", userdata);
            $users = $users.filter((u) => u.id !== userdata.id);
          });

          couchdbEvents.on("chatmessage", function (data) {
            chatmessages.add(data);
          });

          couchdbEvents.on("takein", function (data) {
            onair = true;
            console.table("Take in data: ", data);
            playoutNotifications = `${shortenAuthor(
              data.user,
              ""
            )} is playing now`;
          });

          couchdbEvents.on("takeout", function () {
            onair = false;
          });

          break;
        default:
          throw new NoDataSourceError(`no such datasource "${ds}"`);
      }

      // notication center
      center.init([]);
      // addNotification(notifications.PlayError("kupa kupa 21"), LogLevel.Error);
      // addNotification(notifications.PlayError("something wromg happend"), LogLevel.Error);
    } catch (err) {
      console.error(err);
    }
  });

  // beforeUpdate(() => {
  //   autoscroll =
  //     scrollableElem &&
  //     scrollableElem.offsetHeight + scrollableElem.scrollTop >
  //       scrollableElem.scrollHeight - 20;
  // });

  // afterUpdate(() => {
  //   if (autoscroll) scrollableElem.scrollTo(0, scrollableElem.scrollHeight);
  // });
</script>

<svelte:head>
  <meta charset="utf-8" />
  <meta name="viewport" content="width=device-width, initial-scale=1.0" />
  <meta name="description" content="Breaking News Asharq" />
  <meta
    name="keywords"
    content="Asharq,عاجل,BreakingNews,breaking,Svelte,svelte,svelte.js,sveltejs"
  />
  <meta name="author" content="Tomasz Plonka" />
</svelte:head>

<main class="wrapper">
  <header class="header">
    <div class="notificationarea">
      <Messages count="6" />
      <Chat count="125" user={currentUser} on:emitmsg={handleEmitMessage} />
    </div>
    <div class="fontsize">
      <FontSize on:fontsize={handleFontSize} />
    </div>
    {#if !connected}
      <div class="connection" transition:fade><Connection /></div>
    {/if}
    <div class="logoWrap">
      <img src="build/assets/png/logo.png" alt="Asharq" />
    </div>
    <h1>{'عاجل'}</h1>
  </header>

  <!-- main editor area -->
  <!-- TODO extract a component -->
  <aside class="textinput">
    <!-- a title -->
    <div class="ltr">
      <Modal>
        <SvelteTooltip tip="version 0.22.1" color="#ffffff" right>
          <Content
            bind:this={dialog}
            duration={store2.get("AutoMsgDuration", 15)}
            on:okay={handleOKautoPlay}
          />
        </SvelteTooltip>
      </Modal>
      <label class="rightalign">breaking news group name</label>
    </div>
    <div class="groupname">
      <TitleInput
        title={currentMessage.text}
        disabled
        on:title={handleTitleChange}
      />
      <div><Connection /></div>
    </div>
    <!-- a list of messages -->
    <div id="messages" class="messages">
      <DragDrop
        bind:data={currentMessage}
        CustomComponent={Ainput}
        on:msgfocus={handleFocus}
        on:clicked={handleClick}
        on:keyup={handleKeyUp}
        on:keydown={handleKeyDown}
        on:shouldsave={handleEndDrop}
        on:templateselected={handleTemplateSelected}
      />
      <div class="actionbuttonswrapper">
        <Fab
          label={" + "}
          text="new line"
          on:click={handleFabClick}
          color={"rgba(196, 27, 26, 0.8)"}
          bgColor={"white"}
        />
        <div class="groupwrapper">
          <div class={grouped && !autoplay ? "" : "hidden"}>
            <Fab
              label={""}
              text="remove group"
              on:click={handleUngroup}
              color={"rgba(196, 27, 26, 0.8)"}
              bgColor={"white"}
            />
          </div>
          <div class={selected ? "" : "hidden"}>
            <Fab
              label={""}
              text="add to group"
              on:click={handleGroup}
              color={"rgba(196, 27, 26, 0.8)"}
              bgColor={"white"}
            />
          </div>
          <div class={ingroup ? "" : hidden}>
            <Fab
              label={""}
              text="rm from group"
              on:click={handleRemoveFromGroup}
              color={"rgba(196, 27, 26, 0.8)"}
              bgColor={"white"}
            />
          </div>
        </div>
        <div class="fab {hidden}">
          <Fab
            label=" × "
            text="delete marked"
            on:click={handleDeleteMarked}
            color={"white"}
            bgColor={"rgba(196, 27, 26, 0.8)"}
          />
        </div>
      </div>
    </div>
  </aside>

  <!-- treeview of stories/messages -->
  <nav class="navigation">
    <!-- New div added for folder search -->
    <div class="searchWrap">
      <form on:submit|preventDefault>
        <input type="text" placeholder="بحث" bind:value={searchPhrase} />
      </form>
    </div>
    <!-- File search div ends -->
    <div class="treeWrap">
      <!-- svelte-ignore a11y-invalid-attribute -->
      <div class="fab">
        <Fab
          label={" + "}
          text="new story"
          on:click={getMsg}
          color={"rgba(196, 27, 26, 0.8)"}
          bgColor={"white"}
        />
      </div>
      {#if root.length <= 1}
        <!-- content here -->
        <div class="spinner">
          <Stretch size="40" color="#c41b1a" unit="px" duration="1s" />
        </div>
      {:else}
        <!-- else content here -->
        <Folder
          id={root.id}
          expanded="true"
          text="عاجل"
          messages={filteredRoot}
        />
      {/if}
    </div>
  </nav>

  <!-- playout controls -->
  <div class="select">
    <div class="notifywrapper">
      <Playnotification infotext={playoutNotifications} visible={onair} />
    </div>
    <div class="flex">
      <!-- <button on:click={() => (onair = !onair)}>play</button> -->
      <div class="rightalign {autoplayenabled ? '' : 'invisible'}">
        <section>
          <AutoStopButton
            bind:pressed={autostopping}
            {...autoStopButton}
            on:click={(event) => {
              handleAutoStop(event);
            }}
          />
        </section>
        <section>
          <SvelteTooltip tip="autoplay" color="#ffffff" left>
            <AutoPlayButton
              bind:pressed={autoplay}
              {...autoPlayButton}
              on:click={(event) => {
                handleAutoPlay(event);
              }}
            />
          </SvelteTooltip>
        </section>
      </div>
      <div class="ctlrbuttons leftalign">
        <!-- {#if play || stopping} -->
        <!-- <section class:timer={!play && !autoplay && !stopping} class="hide"> -->
        <section style="visibility: hidden;">
          <Timer bind:this={TimerComponent} />
        </section>
        <!-- {/if} -->
        <section>
          <StopButton
            bind:pressed={stopping}
            {...stopButton}
            on:click={() => handleStopClick()}
          />
        </section>
        <section>
          <PlayButton
            bind:pressed={play}
            {...playButton}
            on:click={(event) => {
              handlePlayClick({ currEditedItemIndex, play: true });
            }}
          />
        </section>
      </div>
    </div>

    <div class="themed" />
  </div>

  <!-- preview area -->
  <footer class="preview" bind:clientWidth={imgW}>
    <Vizrtpreview
      imageWidth={imgW}
      template={selectedTemplate}
      data={datatoRender}
    />
  </footer>
</main>

<style>
  *,
  *:before,
  *:after {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  :global(.newdatamarker) {
    animation: pulse 4s ease-out;
  }

  /* :global(.grouped.container) {
    border-right: 4px solid black !important;
  } */

  :global(.firstitem) {
    border-top: 2px solid black !important;
  }

  :global(.lastitem) {
    border-bottom: 2px solid black !important;
  }

  /* :global(.item .grouped  + .grouped.container) {
    border-top: none !important;
  } */

  @keyframes pulse {
    0%,
    75% {
      background-color: #ddd;
    }
    100% {
      background-color: white;
    }
  }

  /* Works on Firefox */
  * {
    scrollbar-width: thick;
    scrollbar-color: var(--scrollbarred);
  }

  /* Works on Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 16px;
  }

  *::-webkit-scrollbar-track {
    background: #f6f6f6;
  }

  *::-webkit-scrollbar-thumb {
    background-color: var(--scrollbarred);
    border-radius: 0 6px 0 6px;
    /* border: 1px solid rgba(196, 27, 26, 0.5); */
  }

  .hidden {
    display: none;
  }

  .invisible {
    visibility: hidden;
  }

  @font-face {
    font-family: "Bloomberg Arabic Beta v4";
    src: url("assets/fonts/BloombergArabicBetav4-Black.woff2") format("woff2"),
      url("assets/fonts/BloombergArabicBetav4-Black.woff") format("woff");
    font-weight: 900;
    font-style: normal;
  }

  @font-face {
    font-family: "Bloomberg Arabic Beta v4";
    src: url("assets/fonts/BloombergArabicBetav4-Regular.woff2") format("woff2"),
      url("assets/fonts/BloombergArabicBetav4-Regular.woff") format("woff");
    font-weight: normal;
    font-style: normal;
  }

  @font-face {
    font-family: "Bloomberg Arabic Beta v4";
    src: url("assets/fonts/BloombergArabicBetav4-Bold.woff2") format("woff2"),
      url("assets/fonts/BloombergArabicBetav4-Bold.woff") format("woff");
    font-weight: bold;
    font-style: normal;
  }

  .wrapper {
    height: 100vh;
    margin: 0 auto;
    display: grid;
    grid-gap: 0.25rem;
  }

  .navigation {
    background: white;
    min-height: 8vw;
    padding: 1rem;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.19);
    direction: rtl;
    overflow-y: auto;
  }

  .textinput {
    direction: rtl;
    background-color: var(--pagebackground);
    /* padding: 1rem; */
    overflow-y: auto;
    max-height: auto;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    order: 2;
  }

  .textinput label {
    margin-right: 50px;
    font-size: 14px;
    font-weight: 400;
  }

  .textinput * {
    float: right;
  }

  .header {
    background-color: var(--basharqred);
    color: white;
    box-sizing: initial;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
  }

  .header:after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    background: var(--basharqred);
    z-index: 1;
    border-radius: 0 0 0 36px !important;
  }

  .header:before {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    background: white;
    z-index: 0;
    box-shadow: 0px -16px 20px 0px rgba(0, 0, 0, 0.19);
  }

  .header h1 {
    margin: 0;
    padding: 0;
    font-size: 1.5rem;
    font-weight: 900;
    z-index: 2;
  }

  .logoWrap {
    width: 100%;
    text-align: center;
    position: relative;
    z-index: 2;
  }

  .select {
    direction: rtl;
    background-color: white;
    border-radius: 5px;
    height: 2 rem;
    color: black;
    clear: both;
    align-items: center;
    padding: 1rem;
  }

  .preview {
    background-color: black;
    order: 4;
  }

  .flex {
    display: flex;
    align-items: center;
  }

  .spinner {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 75%;
  }

  pre {
    color: black;
  }

  .actionbuttonswrapper {
    padding-top: 1em;
    background: var(--pagebackground);
    width: 100%;
    display: flex;
    align-items: center;
    position: sticky;
    bottom: 0;
  }

  .groupwrapper {
    display: flex;
    margin-left: 2em;
    margin-right: 2em;
  }

  .fab {
    margin-right: 0;
  }

  .notifyBtn {
    width: 28px;
    height: 28px;
    position: relative;
    z-index: 10;
    background: url("assets/svg/bell.svg") no-repeat;
    background-size: 100%;
    cursor: pointer;
    border: none;
    justify-content: right;
  }

  /* unread message count */
  .badge {
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: #4285f4;
    color: #d7e6fd;
    right: 0.8rem;
    border-radius: 9999px;
    font-size: 0.9em;
    min-width: 1.2rem;
    line-height: 1.5em;
