  @media only screen and (min-width: 960px) {
    /* no grid support? */
    .wrapper {
      margin: 0em auto 0em auto;
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 3fr;
      grid-template-rows: auto 1fr auto;
      grid-gap: 0;
    }

    .textinput {
      height: auto;
      overflow-y: hidden;
      max-height: auto;

      display: flex;
      flex-direction: column;
      flex-grow: 1;
      grid-area: 1 / 3 / 3 / 1;
    }

    .header {
      background-color: rgba(196, 27, 26, 0.5);
      /* font-size: 150%; */
      /* needed for the floated layout */
      clear: both;
      grid-area: 2 / 3 / 1 / 3;
    }

    .navigation {
      background-color: white;
      height: auto;
      overflow: hidden;
      min-width: 22vw;
      max-width: 50vw;
      resize: horizontal;
      margin: 0;
      padding: 0;
      grid-area: 3 / 3 / 2 / 3;
    }

    .preview {
      grid-area: 4 / 1 / 3 / 3;
      background-color: black;
      max-height: auto;
      justify-self: stretch;
      /* needed for the floated layout */
      clear: both;
    }

    .preview > img {
      object-fit: cover;
      max-width: 100%;
      max-height: 100%;
    }

    .select {
      background-color: gray;
      border-radius: 5px;
      color: black;
      clear: both;
      align-items: center;
      grid-area: 3 / 3 / 3 / 3;
      border-top: 3px solid rgba(196, 27, 26, 0.5);
      background: white;
      border-radius: 0;
    }

    .actionnotification {
      background-color: var(--basharqred);
      font-weight: 600;
      color: white;
      padding: 0.25em 0.5em;
    }

    .messages {
      /* for select to open when only one message present */
      min-height: 50vh;
      overflow-y: auto;
      overflow-x: hidden;
    }

    .wrapper > *:not(.preview):not(.select) {
      color: white;
      padding: 1rem;
    }
  }

  /* We need to set the widths used on floated items back to auto, and remove the bottom margin as when we have grid we have gaps. */
  @supports (display: grid) {
    .wrapper > * {
      width: auto;
      margin: 0;
    }
  }

