<script>
  export let tags;
</script>

<style>
  .container {
    position: relative;
    line-height: 2rem;
    padding: 0 2.25em 0 0.8em;
    cursor: pointer;
    border-radius: 0 1em 0 1em;
    background-color: white;
    border: 1px solid #e8a5a5;
    box-sizing: border-box;
    font-size: 0.8em;
    font-weight: 700;
    color: #333;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    white-space: nowrap;
    margin: 0.25rem 0.5rem 0 0;
    text-transform: capitalize;
  }

  .container:hover {
    filter: brightness(97%) saturate(120%);
  }

  /* Hide the browser's default checkbox */
  .container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }

  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    top: 9px;
    right: 10px;
    height: 15px;
    width: 15px;
    background: url("assets/svg/unchecked.svg") no-repeat center;
    background-size: 100%;
    border-radius: 50%;
    transition: 0.15s;
  }

  /* On mouse-over, add a grey background color */
  /* .container:hover input ~ .checkmark {
    background-color: #fafafa;
  } */

  /* When the checkbox is checked */
  .container input:checked ~ .checkmark {
    background: url("assets/svg/check.svg") no-repeat center;
    background-size: 100%;
    border: none;
  }

  /* Create the checkmark/indicator (hidden when not checked)
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  } */

  /* Show the checkmark when checked */
  /* .container input:checked ~ .checkmark:after {
    display: block;
  } */

  .wrapper {
    display: flex;
    flex-wrap: wrap-reverse;
    align-items: center;
  }

  * > :first-child {
    margin-right: 0 !important;
  }
</style>

<div class="wrapper">
  {#each tags as tag (tag.id)}
    <label class="container">
      <input
        type="checkbox"
        value={tag.value}
        bind:checked={tag.checked}
        on:click />
      {tag.label}
      <span class="checkmark" />
    </label>
  {/each}
</div>
