<script>
    import * as config from "../.././config.prod";
    import { onMount } from "svelte";
    import moize from "moize";

    import {
        getPreviewUrl,
        GetExtIdFeedUrl,
        GetModelForExtId,
    } from "./vizrtpreview";

    let vizPreview = {
        pdsUrl: config.PilotDataServerUrl,
        prvUrl: config.previewOptions.previewHostUrl,
        extFeedUrl: "",
    };

    // width of the preview image to be requested from Vizrt
    export let imageWidth;
    export let template;

    // TODO allow more complicated data structures as array of strings for example
    // currently one line of text only
    export let data;

    // dynamically generated img src
    let src;
    // image adjustment within the available space,
    // as an image will be cropped
    let xyShift;

    const memoizedGetPreviewUrl = moize.promise(getPreviewUrl);

    $: (async () =>
        (src = await memoizedGetPreviewUrl(
            vizPreview.extFeedUrl,
            template ? template["value"] : undefined,
            ~~imageWidth,
            ~~imageWidth,
            template ? template["stopPoint"] : undefined,
            template ? template["mappings"] : undefined,
            data
        )))();

    $: xyShift = template
        ? `object-position: ${template.hShift} ${template.vShift};`
        : "object-position: 0% 0%;";

    onMount(async () => {
        let resp = await GetExtIdFeedUrl(vizPreview);
        vizPreview.extFeedUrl = resp;

        if (template) {
            let model = await GetModelForExtId(
                template.value,
                vizPreview.extFeedUrl
            );
            vizPreview.modelUrl = model;
        }
    });
</script>

<div>
    <img {src} style={xyShift} alt="Vizrt generated preview here" />
</div>

<style>
    div {
        width: 100%;
    }
    img {
        object-fit: cover;
        width: 100%;
        max-height: 160px;
        object-position: 0% 10%;
    }
</style>
