<script>
    import { getContext } from "svelte";
    export let message;
    export let value;

    export let hasForm = false;

    export let onCancel = () => {};
    export let onOkay = () => {};

    const { close } = getContext("simple-modal");

    let onChange = () => {};

    function _onCancel() {
        onCancel();
        close();
    }

    function _onOkay() {
        onOkay(value);
        close();
    }

    $: onChange(value);
</script>

<style>
    h2 {
        font-size: 1.5rem;
        text-align: center;
    }

    .inputs {
        display: flex;
        flex-direction: column;
        margin: 2rem auto;
        width: 9em;
    }

    input {
        direction: rtl;
        width: 3em;
        padding: 0.25rem;
        font-size: 1.2em;
        line-height: 1.2em;
        margin: 0 auto;
    }

    input:focus {
        outline: 1px dashed var(--basharqred);
        opacity: 1;
        color: black;
    }

    input[type="number"]::-webkit-inner-spin-button {
        opacity: 1;
    }

    label {
        text-align: center;
        margin: 0.5rem;
    }

    .buttons {
        display: flex;
        justify-content: center;
        margin: 3rem 0 1rem 0;
    }

    .close {
        position: absolute;
        top: -2rem;
        right: 0;
        background: black;
    }

    button {
        margin: 0 1rem;
        padding: 0px 20px;
        height: 28px;
        background-color: white;
        border: 1px solid var(--basharqred);
        border-radius: 15px;
        box-sizing: border-box;
        font-family: "Bloomberg Arabic Beta v4", -apple-system,
            BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
            "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !important;
        font-size: 12px;
        font-weight: 400;
        color: var(--basharqred);
        text-align: center;
        line-height: 25px;
        user-select: none;
        font-weight: 700;
    }
    button:hover {
        box-shadow: 0px 4px 6px 0 rgba(102, 102, 102, 0.21);
        cursor: pointer;
    }
    button:active {
        position: relative;
        top: 1px;
    }
</style>

<button class="close" on:click={_onCancel}> Close </button>

<h2>{message}</h2>

{#if hasForm}
    <div class="inputs">
        <label>duration per line (s) </label>
        <input class="seconds" type="number" min="4" max="360" bind:value />
    </div>
{/if}

<div class="buttons">
    <button on:click={_onCancel}> Cancel </button>
    <button on:click={_onOkay}> Yalla! </button>
</div>
