
<script>
    import shortenAuthor from "../ShortenAuthor";

    export let user;
    export let active;
    $: console.log(active);
    $: username = shortenAuthor(user, "");
</script>

<style>
    p {
        color: black;
        font-size: 12px;
        padding: 0.25em 0.45em 0.4em;
        border: 0;
        margin: 0 0.5em 0.5em;
        height: 1.5em;
        font-size: 12px;
        border-radius: 0px 6px 0px 6px;
        line-height: 1rem;
        background-color: var(--greyish);
    }

    .bullet {
        line-height: 1rem;
        font-size: 300%;
        vertical-align: middle;
    }

    .username {
        padding-bottom: 18px;
        vertical-align: middle;
    }

    .active {
        color: var(--activegreen);
    }

    .idle {
        color: var(--idleyellow);
    }

    .inactive {
        color: var(--inactivered);
    }
</style>

<p><span class="bullet {active.value}">•</span><span class="username">{username}</span></p>
